<template>
    <div id="map" class="location__map__container">
    </div>
</template>

<script>
    export default {
        name: "MapApp",
        methods: {
            initMap() {
                const DG = require('2gis-maps');
                const map = DG.map('map', {
                    'center': [54.98, 82.89],
                    'zoom': 13
                });
            }
        },
        mounted() {
            this.initMap()
        }
    }
</script>

<style scoped>
 .location__map__container {
     width: 100%;
     height: calc(100vh/25*11);
 }
</style>
